@font-face {
	font-family: "Blogger-Light";
	src: url("./fonts/Blogger-Light.otf") format("truetype");
	font-style: normal;
	font-weight: normal;
}

@font-face {
	font-family: "Modak";
	src: url("./fonts/Modak.ttf") format("truetype");
	font-style: normal;
	font-weight: normal;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Blogger-Light";
}

:root {
	--sel-color: #62FFFC;
}

body {
	background-color: white;
	height: 100%;
	overflow-x: hidden;
}

.container {
	height: 100%;
	margin: 0 auto;
	padding: 0 0.5rem;
	max-width: 1360px;
	box-sizing: content-box;
}

a {
	text-decoration: none;
	color: inherit;
}

li {
	list-style: none;
}

button {
	border: none;
	background: unset;
}

button:hover {
	cursor: pointer;
}

.hidden {
	display: none !important;
}

/* ------Stylish checkboxes -----------*/
.checkbox {
	cursor: pointer;
	position: relative;
	display: flex;
	flex-direction: row;
	align-items: center;
}

.checkbox input {
	opacity: 0;
}

.checkbox input+span::before {
	text-align: center;
	position: absolute;
	left: 0;
	top: 50%;
	transform: translate(0, -50%);
	content: "";
	border: 0.1vw solid grey;
}

.checkbox input:checked+span::before {
	content: '\2714';
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	color: white;
	background-color: orange;
	border: 0.1vw solid orange;
}



/* ------------Scrollbar style------------ */
div::-webkit-scrollbar,
menu::-webkit-scrollbar,
ul::-webkit-scrollbar {
	width: 0.3vw;
	background-color: rgba(192, 192, 192, 1);
}


div::-webkit-scrollbar-thumb,
menu::-webkit-scrollbar-thumb,
ul::-webkit-scrollbar-thumb {
	background-color: rgba(128, 128, 128, 0.7);
	border-radius: 9em;
}

div::-webkit-scrollbar-thumb:hover,
menu::-webkit-scrollbar-thumb:hover,
ul::-webkit-scrollbar-thumb:hover {
	background-color: rgba(128, 128, 128, 1);
}

/* mozilla */
div,
menu,
ul {
	scrollbar-color: rgba(128, 128, 128, 0.7) rgba(225, 225, 225, 0.5);
	scrollbar-width: thin !important;
}


@media only screen and (min-width: 769px) {
	.checkbox span {
		margin-left: 0.7vw;
	}

	.checkbox input {
		height: 1vw;
		width: 1vw;
	}

	.checkbox input+span::before {
		height: 1vw;
		width: 1vw;
		border-radius: 0.2vw;
	}

	.srch-hint {
		padding: 1vw 0;
		border-radius: 1vw;
	}

	.srch-hint-item {
		font-size: 1vw;
		padding: 0.5vw 1vw;
	}

	.srch-hint-item em {
		font-size: 1.2vw;
	}
}

@media only screen and (max-width: 768px) {
	.checkbox span {
		margin-left: 0.7vw;
	}

	.checkbox input {
		height: 1vw;
		width: 1vw;
	}

	.checkbox input+span::before {
		height: 1vw;
		width: 1vw;
		border-radius: 0.2vw;
	}

	.srch-hint {
		padding: 1vw 0;
		border-radius: 1vw;
	}

	.srch-hint-item {
		font-size: 1vw;
		padding: 0.5vw 1vw;
	}

	.srch-hint-item em {
		font-size: 1.2vw;
	}
}



